import links from '@/components/common/links'
import ads_footer from "@/components/ads/ads_footer";

export default {
  name: 'appfooter',
  components: {
    links,
    ads_footer
  },
  data() {
    return {}
  },
  computed: {
    download_app_url: function () {
      var app_conf = this.applist_conf();
      console.log('app_conf:', app_conf);
      if (app_conf) {
        return app_conf['url'];
      }
      return '/';
    }
  },
